@import "../../../styles/variables.scss";

.addButton {
  width: 100%;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;

  background-color: $color-primary;
  color: white;
  transition: box-shadow 0.3s;

  border: none;
  padding: 0;
  cursor: pointer;
  outline: inherit;

  font-weight: 700;
  font-size: 14px;
  line-height: 21px;

  &:hover {
    box-shadow: 0px 3px 13px rgba(23, 32, 49, 0.4);
  }

  &_selectedButton {
    color: #27ae60;
    background-color: $color-white;
    border: 1px solid $color-submarine;
  }
}
